// 01  vue  vue router
import Vue from 'vue'
import Router from 'vue-router'
// 02  
import home from '@/page/home/home.vue'
import category from '@/page/category/category.vue'
import detail from '@/page/detail/detail.vue'
import des from '@/page/detail/des.vue'
import arg from '@/page/detail/arg.vue'
// 03  Vue 
Vue.use(Router)

// 04 
export default new Router({
  routes: [
    {
      path: '/',        //  / home
      redirect:"/home"
    },
    {
      path: '/home',        // 
      name: 'home',     // 
      component: home,   // 
      alias:"/index"
    },
    {
      path: '/category',
      name: 'category',
      component: category
    },
    // 
    {
      path: '/detail/:id',
      name: 'detail',
      component: detail,
    // children
      children:[
        {path: '', redirect: 'des'},//  des
        {path:"des",
          component:des
        },
        {path:"arg",
          component:arg
        }
      ]
    }
  ]
})



